.card {
    width: 300px;
    height: auto;
    transition: all 0.2s;
    /* position: relative; */
    cursor: pointer;
    margin: 50px;
  }
  
  .card-inner {
    width: inherit;
    height: inherit;
    background: white;
    box-shadow: 0.4rem 0.4rem 0.5rem #6d5dfc, 0.0rem 0.0rem 0.0rem rgb(63, 63, 63);
    backdrop-filter: blur(10px);
    border-radius: 8px;
    cursor: auto;
    border: 2px solid rgb(76, 76, 76);
  }
  
  .card:hover {
    transform: scale(1.05) rotate(-1deg);
  }
  
.readmore{
  color : #E4EBF5;
  /* margin-top: 14%;
  margin-left: 9%; */
  background-color: transparent;
  border: none;
  width: 100px;
  height: 50px;
  font-size: 14px;
}

  .circle {
    width: 100px;
    height: 50px;
    background: radial-gradient(#8fb3ff, #7730fd);
    border-radius: 10px;
    position: absolute;
    animation: move-up6 2s ease-in infinite alternate-reverse;
  }
  
  .circle:nth-child(1) {
    width: auto;
    padding-top: 12px;
    padding-left: 10px;
    padding-right: 10px;
    color: whitesmoke;
    top: -40px;
    left: -40px;
  }
  
  .circle:nth-child(2) {
    bottom: -40px;
    margin-left: 80%;
    animation-name: move-down1;
  }
  .image{
    margin: 2px;
    height: 150px;
    width: 292px;
    border-radius: 8px;
    cursor: pointer;
  }

  hr{
    margin-top: 2px;
    height: 1px;
    background-color:rgb(76, 76, 76);
  }

  .url{
    text-decoration: none;
    color : whitesmoke;
  }

  .url:hover{
    text-decoration: none;
    color : var(--greyLight-3);
  }
  .title{
    padding-left: 7px;
    padding-right: 7px;
    color: var(--primary-light);
  }
  .description{
    padding-left: 7px;
    padding-right: 7px;
  }

  .date{
    padding-left: 7px;
    padding-right: 7px;
    padding-bottom: 7px;
  }
  @keyframes move-up6 {
    to {
      transform: translateY(-10px);
    }
  }
  
  @keyframes move-down1 {
    to {
      transform: translateY(10px);
    }
  }