:root {
  --primary-light: #8abdff;
  --primary: #6d5dfc;
  --primary-dark: #5b0eeb;
  --white: #ffffff;
  --greyLight-1: #e4ebf5;
  --greyLight-2: #c8d0e7;
  --greyLight-3: #bec8e4;
  --greyDark: #9baacf;
}

.navbar {
  background-color: rgb(234, 234, 234);
  border-radius: 0 0 2rem 2rem;
  box-shadow: 0.1rem 0.1rem 0.7rem var(--primary), -0.1rem -0.1rem 0.1rem var(--white);
  position: sticky;
  top: -10px;
  z-index: 10;
}

.logoname {
  background-color: var(--white);
  color: var(--primary);
  border-radius: 1rem;
  box-shadow: 0.1rem 0.1rem 0.2rem var(--primary), -0.0rem -0.0rem 0.0rem black;
  margin: 10px;
  padding: 10px;
}

.logoname:hover {
  background-color: var(--white);
  color: var(--greyDark);
  border-radius: 1rem;
  box-shadow: 0.1rem 0.1rem 0.2rem var(--primary), -0.0rem -0.0rem 0.0rem black;
  margin: 10px;
  padding: 10px;
}

.hambtn {
  box-shadow: 0.1rem 0.1rem 0.2rem var(--primary), -0.0rem -0.0rem 0.0rem black;
}

.hambtn:focus {
  outline: none;
}

.hambtn:active {
  box-shadow: none;
  transition: 300ms ease-in-out;
}

.hamburger {
  margin-top: 2px;
  margin-bottom: 2px;
  width: 20px;
  height: 15px;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  cursor: pointer;
}

.bar {
  width: 100%;
  height: 2px;
  background-color: var(--primary);
}

.nav-link {
  color: var(--primary);
  margin: 10px;
  text-align: center;
}

.nav-link:hover {
  background-color: var(--white);
  color: var(--primary);
  border-radius: 1rem;
  box-shadow: 0.1rem 0.1rem 0.2rem var(--primary), -0.0rem -0.0rem 0.0rem black;
}

.nav-link:active {
  box-shadow: none;
  transition: 200ms ease-in-out;
  background-color: rgb(234, 234, 234);
}

.navbar-nav li.active > a {
  background-color: var(--white);
  color: var(--primary);
  border-radius: 1rem;
  box-shadow: 0.1rem 0.1rem 0.2rem var(--primary), -0.0rem -0.0rem 0.0rem black;
}

.alrpos {
  display: flex;
  justify-content: flex-end;
  align-items: baseline;
  position: absolute;
  right: 0px;
  bottom: 100px;
}
